<template>
    <div>
        <div class="controls">
            <button data-val="87"  v-touch:start="touchDown"  v-touch:end="touchUp" class="controls__up">Up</button>
            <button data-val="65"  v-touch:start="touchDown"  v-touch:end="touchUp" class="controls__left">Left</button>
            <button data-val="83"  v-touch:start="touchDown"  v-touch:end="touchUp" class="controls__down">Down</button>
            <button data-val="68"  v-touch:start="touchDown"  v-touch:end="touchUp" class="controls__right">Right</button>
        </div>
    </div>
</template>

<script>
    import { mapActions } from "vuex"
    export default {
        name: "MobileControls",
        methods: {
            touchDown(e) {
              this.$store.dispatch("mobileControl", e.target.dataset.val)
            },
            touchUp() {
                this.$store.dispatch("mobileControl", "stop")
            }


        }
    }
</script>

